@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,600;0,700;1,400;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700;800;900&display=swap);
html {
    --color-primary:#3F3F3F;
    --color-secondary:#FFFFFF;
    --color-primary-alt:#545454;
    
}

html[data-theme=light] {
    --primary:#FFFFFF;
    --color-secondary:#3F3F3F;
    --color-primary-alt:#EFEFEF
  }
:root {
  --spacing-xs: 10px;
  --spacing-s: 20px;
  --spacing-m: 30px;
  --spacing-l: 40px;
  --spacing-xl: 50px;
}

:root{
  /* size */
--font-small: .8rem;
--font-paragraph: 14px;
--font-paragraphLarge: 1.3rem;
--font-h3: 1.25rem;
--font-h2: 1.5rem;
--font-h1: 2rem;

/* Bold */
--regular: 400;
--medium: 500;
--bold: 700;
--black: 900;
}
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Nunito", sans-serif;
}

:root {
  --border-radius: 10px;
  --box-shadow: 0 1px 3px rgba(0, 0, 0, .5);
}

.App {
  text-align: center;
  margin-top: 60px;
  height:calc(100vh - 60px);
  overflow: auto;
  background-color: var(--color-primary);
}

/* .App section{
  height:calc(100vh - 60px);
  padding-top: 1px;
} */
.navbar {
  position: fixed;
  width: 100%;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  background-color: var(--color-primary-alt);
  box-shadow: var(--box-shadow);
}

.navbar__logo {
  cursor: pointer;
  display: flex;
  margin-left: 20px;
}

.navbar__menu {
  display: flex;
  align-items: center;
  margin-right: 40px;
}

.navbar__item {
  transition: all 0.1s;
  position: relative;
  text-decoration: none;
}

.navbar__item:active {
  transform: scale(0.92);
}

.navbar-item {
  width: 137px;
  height: 40px;
  overflow: hidden;
  transition: all 0.3s;
}
.navbar-item img {
    height: 40px;
    transform: translate(0px);
}
.navbar-item:hover img {
  cursor: pointer;
  position: relative;
  transform: translate(-144px);
}

@-webkit-keyframes intro {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes intro {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.banner{
    height: 70vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: url(/static/media/background_banner_dark_theme.803efac1.png) no-repeat;
    background-size: cover;
}

.banner img{
    width: 400px;
}
.about {
  height: 70vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 0.5fr 1.5fr 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-template-areas:
    "about__title about__title about__title about__title about__title"
    "about__text about__text about__text about__face-image about__face-image"
    "about__text about__text about__text about__face-image about__face-image";
  color: var(--color-secondary);
  padding: 30px 70px;
}

.about__title {
  grid-area: about__title;
  justify-self: start;
  align-self: end;
}

.about__text {
  grid-area: about__text;
  align-self: center;
}
.about__text P {
  text-align: start;
  margin: var(--spacing-s);
}

.about__face-image {
  grid-area: about__face-image;
  align-self: center;
}

.about__face-image img {
  width: 400px;
}

.projects {
  background-color: var(--color-primary-alt);
  display: grid;
  grid-gap: 0px 0px;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  color: var(--color-secondary);
  padding: 30px 70px;
}

.projects__title {
  justify-self: start;
  margin: 44px 0;
}

.projects__cards {
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  justify-content: center;
}

@media (min-width: 1068px) {
  .projects__cards {
    grid-template-columns: repeat(3, 331px);
  }
}
.button{
    padding: 10px 20px;
    border-radius: 50px;
    border: none;
    color: var(--color-secondary);
    text-shadow: var(--box-shadow);
    box-shadow: var(--box-shadow);
    background-image: linear-gradient(to right, #2b2b2b ,  #727272);
    transition: all .06s;
    outline-color: #727272;
}
.button:hover{
  cursor: pointer;
  transform: scale(1.1);
}
.button:active{
  transform: scale(1);
}

.button.button--html {
background-image: linear-gradient(to right, #E44D26 , #FF8D00);
outline-color:  #FF8D00;


}

.button.button--css {
background-image: linear-gradient(to right, #264DE4 ,  #26BAE3);
}

.button.button--js {
background-image: linear-gradient(to right, #FFC800 ,  #FFFF35);
}

.button.button--node {
background-image: linear-gradient(to right, #3DAE2B ,  #3DE12B);
}
.project-card {
  position: relative;
  width: 331px;
  height: 373px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border-radius: 4px;
  background-color: var(--color-primary);
  box-shadow: var(--box-shadow);
  box-sizing: border-box;
  overflow: hidden;
  transition: all 0.3s;
  /* padding: 10px; */
  position: relative;
}

.project-card h3 {
 margin-left: 10px;
}
.project-card__techs {
  width: 100%;
  display: flex;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}
.project-card__logo-tech {
  height: 35px;
  width: auto;

}

.project-card__info{
  height: 100%;
  width: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.404);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  align-items: center;
  bottom: -369px;
  transition: all .1s;

}
.project-card__info p{
 width: 80%;
 text-align: start;
 margin-top: 20px;
 text-shadow: var(--box-shadow);
}
.project-card__info a{
  position: absolute;
  bottom: 20px;
  cursor: pointer;

}

.project-card:hover .project-card__info{
  bottom: 0;
  transition: all .1s;
}
